import React from 'react'
import { Field, reduxForm } from 'redux-form'
import validate from './validate'
import textField from '../utils/textField'
import fileField from '../utils/fileField'
import NextBtn from '../utils/NextBtn'


const WizardFormFourthPage = props => {
  const { handleSubmit } = props
  return (
    <form onSubmit={handleSubmit} encType="multipart/form-data"  acceptCharset="UTF-8" method="post">
      <Field component={fileField} name="avatar" />
      <NextBtn text="Continue" onClick={this.onClickSave} />
    </form>
  )
}

export default reduxForm({
  form: 'wizard',
  destroyOnUnmount: false,
  forceUnregisterOnUnmount: true,
  validate
})(WizardFormFourthPage)



