import React from 'react'

class NextBtn extends React.Component {
  constructor(props) {
    super(props)
  }

  render() {
    const {text} = this.props
    return (
      <div>
        <button type="submit" className="Button CenterAling">
          {text}
        </button>
      </div>
    )
  }
}

export default NextBtn



