import React, {Component} from 'react'
import Dropzone from 'react-dropzone'
import NextBtn from '../utils/NextBtn'

export class fileField extends Component {

  handleDropOrClick = (acceptedFiles, rejectedFiles, e) => {
    let eventOrValue = e;
    let {input: {onChange, onBlur}} = this.props;
    if (e.type === 'drop') {
      if (acceptedFiles.length) {
        // FileList or [File]
        eventOrValue = (e.dataTransfer && e.dataTransfer.files) || acceptedFiles;
      } else {
        eventOrValue = null;
      }
    }
    onBlur(eventOrValue); // update touched
    onChange(eventOrValue); // update value
  }

  render() {
    let {input, meta: {touched, error}} = this.props;
    let {accept, multiple} = this.props;
    let selectedFile = (input && input.value && input.value[0]) || null;
    let dropzoneProps = {
      accept,
      multiple,
      className: "DropArea flex items-center",
      onDrop: this.handleDropOrClick,
    };

    return (
      <div>
        <input type='hidden' disabled {...input} />
        {touched &&
          error &&
          <span className="error">
            {error}
          </span>}
        <Dropzone {...dropzoneProps}>
          <p>Dropping some picture of you here, or click to select one to upload.</p>
        </Dropzone>
        {selectedFile? <img src={selectedFile.preview} width="30" className="DroppedImage" /> : null}
      </div>
      );
  }
}

export default fileField



